      <div class="card-header">
        <h3>âš™ï¸ è¾“å‡ºè®¾ç½®</h3>
        <p>è®¾ç½®è¾“å‡ºè§†é¢‘çš„æ ¼å¼å’Œè´¨é‡</p>
      </div>

      <div class="output-settings">
        <div class="setting-item">
          <label>è¾“å‡ºæ ¼å¼</label>
          <el-select v-model="outputFormat">
            <el-option label="MP4 (æ¨è)" value="mp4" />
