          </el-select>
        </div>

        <div class="setting-item">
          <label>è§†é¢‘è´¨é‡</label>
          <el-select v-model="videoQuality">
            <el-option label="é«˜è´¨é‡ (åŸå§‹)" value="high" />
            <el-option label="ä¸­ç­‰è´¨é‡" value="medium" />
            <el-option label="ä½è´¨é‡ (å‹ç¼©)" value="low" />
          </el-select>
        </div>

        <div class="setting-item">
          <label>æ–‡ä»¶å‘½å</label>
          <el-radio-group v-model="namingMode">
