          <span class="tip-text">ä¸Šä¼ è§†é¢‘æ–‡ä»¶ï¼Œæ”¯æŒå¸¸è§çš„è§†é¢‘æ ¼å¼</span>
        </div>
        <div class="tip-item">
          <span class="tip-icon">â°</span>
          <span class="tip-text">è®¾ç½®è¦æˆªå–çš„æ—¶é—´æ®µï¼Œå¯ä»¥æ·»åŠ å¤šä¸ªç‰‡æ®µ</span>
        </div>
        <div class="tip-item">
          <span class="tip-icon">ğŸ¯</span>
          <span class="tip-text">ä½¿ç”¨"å½“å‰æ—¶é—´"æŒ‰é’®å¿«é€Ÿè®¾ç½®æ’­æ”¾ä½ç½®çš„æ—¶é—´</span>
        </div>
        <div class="tip-item">
          <span class="tip-icon">ğŸ‘€</span>
          <span class="tip-text">é¢„è§ˆç‰‡æ®µç¡®è®¤æˆªå–èŒƒå›´æ˜¯å¦æ­£ç¡®</span>
